import Vue from 'vue'
import Router from 'vue-router'
import JobTimeline from '../components/JobTimeline'
import Home from '../components/resume'
import ShuffleNode from '../components/demo/shufflenode'
import demo from '../components/demo/index'
import todolist from '../components/demo/todolist'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/home'
    },
    {
      path: '/home',
      component: Home
    },
    {
      path: '/JobTimeline',
      component: JobTimeline
    },
    {
      path: '/demo',
      component: demo
    },
    {
      path: '/shufflenode',
      component: ShuffleNode
    },
    {
      path: '/todolist',
      component: todolist
    }
  ]
})



