
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import _ from 'lodash'
export default {
name: 'shufflenode',
components: {
XButton,
XHeader
},
data () {
return {
items: [1, 2, 3, 4, 5, 6, 7, 8, 9],
board: []
}
},
methods: {
shuffle: function () {
this.items = _.shuffle(this.items)
console.log(this.items)
},
sleep: function (ms) {
return new Promise(resolve => setTimeout(resolve, ms))
},
quicksort: async function quicksort (start, end) {
if ((end - start) <= 0) { return }
console.log(this.items, start, end)
let mid = this.items[start]
this.board.push(mid)
let midindex = start
let left = start + 1
let right = end
while (left <= right) {
if (this.items[left] < mid) {
let tmp = this.items[midindex]
this.items[midindex] = this.items[left]
this.items[left] = tmp
await this.sleep(1000)
this.$forceUpdate()
midindex += 1
left += 1
} else {
let tmp = this.items[right]
this.items[right] = this.items[left]
this.items[left] = tmp
await this.sleep(1000)
this.$forceUpdate()
right -= 1
}
}
this.quicksort(start, midindex - 1)
this.quicksort(midindex + 1, end)
},
restart: function () {
this.board = []
this.items = this.items.sort()
}
}
}



