
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'

var filters = {
all: function (todolists) {
return todolists
},
active: function (todolists) {
return todolists.filter(function (todo) {
return !todo.completed
})
},
completed: function (todolists) {
return todolists.filter(function (todo) {
return todo.completed
})
}
}
export default {
name: 'TodoList',
components: {
XButton,
XHeader,
XInput
},
data () {
return {
todolists: [
{
uid: 0,
value: '上班',
completed: false
}
],
uid: 0,
edittodo: '',
mode: 'all'
}
},
methods: {
change: function (val, label) {
console.log(val, 'xxx', label)
},
changemode: function (mode) {
this.mode = mode
},
addtodo: function () {
this.todolists.push({
uid: this.uid++,
value: this.edittodo,
completed: false
})
this.edittodo = ''
},
removetodo: function (todo) {
this.todolists.splice(this.todolists.indexOf(todo), 1)
},
clearedit: function () {
this.edittodo = ''
},
clearcompleted: function () {
this.todolists = filters.active(this.todolists)
}
},
computed: {
showlist: function () {
return filters[this.mode](this.todolists)
},
remain: function () {
return filters['completed'](this.todolists).length
}
}
}



